@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);
/* http://meyerweb.com/eric/tools/css/reset/
   v4.0 | 20180602
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block; }

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

html, body, #root {
  height: 100%; }

body {
  background-color: #edf1f8;
  font-family: 'Roboto', sans-serif;
  color: #707090; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.text-left {
  text-align: left; }

.flex {
  display: flex; }

.inline-flex {
  display: inline-flex; }

.flex-direction-column {
  flex-direction: column; }

.justify-content-space-between {
  justify-content: space-between; }

.fancy-scrollbar::-webkit-scrollbar {
  height: 8px;
  width: 6px; }

.fancy-scrollbar::-webkit-scrollbar-track-piece {
  background: rgba(9, 45, 66, 0.08); }

.fancy-scrollbar::-webkit-scrollbar-thumb {
  background-color: #7a838f;
  border-radius: 3px; }

.search {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  padding: 0 23px; }
  .search .icon {
    padding: 0 11px 0 0; }
  .search input {
    color: inherit;
    font-size: inherit;
    width: 250px;
    background: transparent;
    border: none;
    outline: none; }
    .search input::-webkit-input-placeholder {
      color: inherit; }
    .search input::-ms-input-placeholder {
      color: inherit; }
    .search input::placeholder {
      color: inherit; }

.ProfileCircle {
  display: flex;
  justify-content: center;
  width: 41px;
  height: 41px;
  background-color: #c9ced5;
  border-radius: 20.5px;
  margin: 0 0 0 22px; }

.user-profile ul {
  display: flex;
  height: 100%; }
  .user-profile ul li {
    display: flex;
    align-items: center;
    padding: 0 20px;
    border-left: 1px solid #ffffff; }
    .user-profile ul li:first-child {
      border-left: none; }
    .user-profile ul li:last-child {
      padding-right: 0; }
  .user-profile ul .username {
    white-space: nowrap; }

header {
  display: flex;
  height: 60px;
  flex-grow: 0;
  flex-shrink: 0;
  color: #ffffff;
  font-size: 14px; }
  header .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 120px;
    background-color: #878fdc; }
    header .logo h1 {
      font-size: 20px;
      font-weight: bold;
      color: #ffffff; }
  header nav {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    padding-right: 40px;
    width: 100%;
    height: 100%;
    background-image: -webkit-linear-gradient(left, #9ba8fb, #70b3f8);
    background-image: linear-gradient(to right, #9ba8fb, #70b3f8); }


.main {
  display: flex;
  align-items: stretch;
  flex-grow: 1; }
  .main menu {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: 120px;
    background-color: #ffffff;
    box-shadow: 4px 3px 20px #7d8cab36;
    color: #c9ced5;
    font-size: 14px;
    overflow-y: auto; }
    .main menu ul {
      text-align: center; }
      .main menu ul li {
        padding: 25px 0;
        cursor: pointer; }
        .main menu ul li .caption {
          margin-top: 11px; }
        .main menu ul li.selected {
          border: 4px solid green;
          border-top: 0;
          border-right: 0;
          border-bottom: 0;
          border-image-source: linear-gradient(#9ba8fb, #70b3f8);
          border-image-slice: 4;
          color: #7a838f; }
          .main menu ul li.selected .caption {
            font-weight: bold; }
    .main menu .settings {
      text-align: center;
      padding: 26px 0; }
      .main menu .settings .caption {
        margin-top: 11px; }

.App {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100%;
}

